:root {
  --dark-cyan-color: #3c8067;
  --cream-color: #f2ebe3;
  --very-dark-blue-color: #1c232b;
  --dark-grayish-blue-color: #6c7289;
  --deep-aquamarine-color: #1a4233;
  --white-color: #fff;
  --card-width: 21.4375rem;
  --card-height: 38.1875rem;
  --card-content-space: 1.5rem;
  --card-border-radius: .625rem;
  --card-category-basis: 1.6875rem;
  --card-title-basis: 5rem;
  --card-description-basis: 7.25rem;
  --category-text-size: .75rem;
  --category-letter-spacing: .3125rem;
  --small-text-size: .8125rem;
  --small-line-height: 1.77;
  --large-text-size: 2rem;
  --large-line-height: 1;
  --standard-text-size: .875rem;
  --standard-line-height: 1.64;
  --image-width: var(--card-width);
  --image-height: 15rem;
  --price-list-space: 1.19rem;
  --button-width: 18.4375rem;
  --button-height: 3rem;
  --button-border-radius: .5rem;
  --button-margin-top: 1.25rem;
}

html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body, h1, h2, h3, a, p {
  margin: 0;
  padding: 0;
}

body {
  background: var(--cream-color);
  font-family: Montserrat, sans-serif;
}

.container {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.card {
  width: var(--card-width);
  height: var(--card-height);
  flex-flow: column;
  display: flex;
}

.attribution {
  text-align: center;
  color: var(--dark-grayish-blue-color);
}

.attribution h1 {
  color: var(--dark-cyan-color);
  margin: 0 0 1rem;
  font-size: 1.2rem;
}

.attribution a {
  color: var(--very-dark-blue-color);
  text-decoration: none;
}

.attribution a:hover {
  text-decoration: underline;
}

.card__asset {
  flex-basis: var(--image-height);
  border-radius: var(--card-border-radius) var(--card-border-radius) 0 0;
  background: url("image-product-mobile.6dd5cee3.jpg") 0 0 / contain no-repeat;
}

.card__content {
  padding: var(--card-content-space);
  flex-flow: column;
  flex-basis: calc(var(--card-height)  - var(--image-height));
  background: var(--white-color);
  border-radius: 0 0 var(--card-border-radius) var(--card-border-radius);
  display: flex;
}

.card__title, .card__price--discounted {
  font-family: Fraunces, serif;
}

.card__category {
  text-transform: uppercase;
  font-weight: 500;
  font-size: var(--category-text-size);
  letter-spacing: var(--category-letter-spacing);
  color: var(--dark-grayish-blue-color);
  flex-basis: var(--card-category-basis);
}

.card__title {
  font-weight: 700;
  font-size: var(--large-text-size);
  line-height: var(--large-line-height);
  color: var(--very-dark-blue-color);
  flex-basis: var(--card-title-basis);
}

.card__description {
  font-weight: 500;
  font-size: var(--standard-text-size);
  line-height: var(--standard-line-height);
  color: var(--dark-grayish-blue-color);
  flex-basis: var(--card-description-basis);
}

.card__price {
  flex-basis: var(--card-price-basis);
  align-items: center;
  gap: var(--price-list-space);
  display: flex;
}

.card__price--discounted {
  font-weight: 700;
  font-size: var(--large-text-size);
  line-height: var(--large-line-height);
  color: var(--dark-cyan-color);
}

.card__price--original {
  color: var(--dark-grayish-blue-color);
  font-weight: 500;
  font-size: var(--small-text-size);
  line-height: var(--small-line-height);
  text-decoration-line: line-through;
}

.card__button {
  margin-top: var(--button-margin-top);
  border-radius: var(--button-border-radius);
  font-family: inherit;
  font-weight: 700;
  font-size: var(--standard-text-size);
  width: var(--button-width);
  height: var(--button-height);
  color: var(--white-color);
  background: var(--dark-cyan-color);
  border: .1rem solid var(--dark-cyan-color);
  justify-content: center;
  align-items: center;
  gap: var(--standard-text-size);
  flex-flow: row;
  display: flex;
}

.card__button:hover {
  cursor: pointer;
  background: var(--deep-aquamarine-color);
}

@media screen and (width >= 90em) {
  :root {
    --card-width: 37.5rem;
    --card-height: 28.125rem;
    --card-content-space: 2rem;
    --card-category-basis: 2.1875rem;
    --card-title-basis: 7.5rem;
    --card-description-basis: 7.56rem;
    --image-width: calc(var(--card-width) / 2);
    --button-width: 14.75rem;
    --button-margin-top: 1.88rem;
  }

  .card {
    flex-flow: row;
  }

  .card__asset {
    flex-basis: var(--image-width);
    border-radius: var(--card-border-radius) 0 0 var(--card-border-radius);
    background: url("image-product-desktop.1af46163.jpg") 0 0 / cover no-repeat;
  }

  .card__content {
    border-radius: 0 var(--card-border-radius) var(--card-border-radius) 0;
  }
}

